*, :after, :before {
    box-sizing: border-box;
}
body {
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: #000;
}
.range-filter {
    width: 100%;
    max-width: 800px;
}
.range-controls {
    position: relative;
    background-color: #f1f1f1;
    padding: 40px 0;
}
.range-controls .scale {
    height: 2px;
    background-color: #d7dcde;
}
.range-controls .bar {
    height: 2px;
    background-color: #00ca74;
}
.range-controls .toggle{
    position: absolute;
    top: 32px;
    width: 4px;
    height: 4px;
    border: 8px solid gray;
    background-color: #ababab;
    border-radius: 50%;
    cursor: pointer;
}